import React from "react";
import SmogPredictions from "../components/smog/SmogPredictions";

function Smog(props) {
  return (
    <div>
      <div className="event-header">
        <h1>Przewidywane poziomy smogu</h1>
      </div>
      <h3 className="smog">
        Planujesz wyjcie na nadchodzce wydarzenie w Warszawie? We pod uwag
        przewidywane poziomy smogu w cigu najbliszych dni.
      </h3>
      <SmogPredictions />
      <h3 className="smog">
        Normy ste pyu zawieszonego zalecane przez wiatow Organizacj
        Zdrowia (WHO):
      </h3>
      <h3 className="smog">
        <ul>
          <li>norma redniego dziennego stenia pyu PM10: 50 g/m3</li>
          <li>norma redniego rocznego stenia pyu PM10: 20 g/m3</li>
        </ul>
      </h3>
    </div>
  );
}

export default Smog;
